<template>
  <div>
    <!-- <Modal v-model="createOrderModal" width="400">
      <p slot="header" style="text-align: center; font-size: 20px; color: cornflowerblue;">
        <span>Create Order</span>
      </p>
      <Form :model="createCouponForm" style="margin:auto">
        <FormItem>
          <Input
          v-model="createCouponForm.coupon_id"
          show-word-limit
          type="textarea"
          placeholder="Enter something..."
          maxlength="1"
          :autosize="{minRows: 1}"></Input>
        </FormItem>
        <FormItem>
          <Input
          v-model="createCouponForm.discount"
          type="number"
          placeholder="Enter something..."
          :autosize="{minRows: 1}"></Input>
        </FormItem>
        <FormItem>
          <Input
          v-model="createCouponForm.inventory"
          type="number"
          placeholder="Enter something..."
          :autosize="{minRows: 1}"></Input>
        </FormItem>
      </Form>
      <div slot="footer">
      	<Button type="primary" size="large" long @click="submit()">Submit</Button>
      </div>
    </Modal> -->
    <h1 style="margin-top:10px; text-align: center;">Order</h1>
    <Table height="500" border :columns='orderColumns' :data='orderData' style="margin-top: 20px;">
      <template slot-scope="{ row, index }" slot="order_id">
        <Input type="text" v-model="editOrder_id" v-if="editIndex === index" />
        <span v-else>{{ row.order_id }}</span>
      </template>

      <template slot-scope="{ row, index }" slot="buyer_account_id">
        <Input type="text" v-model="editBuyer_account_id" v-if="editIndex === index" />
        <span v-else>{{ row.buyer_account_id }}</span>
      </template>

      <template slot-scope="{ row, index }" slot="good_name">
        <Input type="text" v-model="editGood_name" v-if="editIndex === index" />
        <span v-else>{{ row.good_name }}</span>
      </template>

      <template slot-scope="{ row, index }" slot="size">
        <Input type="text" v-model="editSize" v-if="editIndex === index" />
        <span v-else>{{ row.size }}</span>
      </template>

      <template slot-scope="{ row, index }" slot="produce_place">
        <Input type="text" v-model="editProduce_place" v-if="editIndex === index" />
        <span v-else>{{ row.produce_place }}</span>
      </template>

      <template slot-scope="{ row, index }" slot="amount">
        <Input type="number" v-model="editAmount" v-if="editIndex === index" />
        <span v-else>{{ row.amount }}</span>
      </template>

      <template slot-scope="{ row, index }" slot="year">
        <Input type="number" v-model="editYear" v-if="editIndex === index" />
        <span v-else>{{ row.year }}</span>
      </template>

      <template slot-scope="{ row, index }" slot="month">
        <Input type="number" v-model="editMonth" v-if="editIndex === index" />
        <span v-else>{{ row.month }}</span>
      </template>

      <template slot-scope="{ row, index }" slot="day">
        <Input type="number" v-model="editDay" v-if="editIndex === index" />
        <span v-else>{{ row.day }}</span>
      </template>

      <template slot-scope="{ row, index }" slot="price">
        <Input type="number" v-model="editPrice" v-if="editIndex === index" />
        <span v-else>{{ row.price }}</span>
      </template>

      <template slot-scope="{ row, index }" slot="action">
        <Button type="error" size="small" @click="remove(index)">移除</Button>
      </template>
    </Table>
    <Button type="info" style="margin-top: 10px; margin-left: 10px; width: 100px;" v-on:click="upLoad">
      <Icon type="ios-cloud" size="20"></Icon>
      Upload
    </Button>
  </div>
</template>

<script>
  //import data from '../mock/order.js'
  export default {
    props:{
      newOrderList: Array,
      dataSource: Number
    },
    data() {
      return {
        createOrderModal: false,
        createOrderForm: {

        },
        orderColumns: [{
            title: 'order_id',
            key: 'order_id',
            sortable: true
          },
          {
            title: 'buyer_account_id',
            key: 'buyer_account_id',
            sortable: true
          },
          {
            title: 'good_name',
            key: 'good_name',
            sortable: true
          },
          {
            title: 'size',
            key: 'size',
            sortable: true
          },
          {
            title: 'produce_place',
            key: 'produce_place',
            sortable: true
          },
          {
            title: 'amount',
            key: 'amount',
            sortable: true
          },
          {
            title: 'year',
            key: 'year',
            sortable: true
          },
          {
            title: 'month',
            key: 'month',
            sortable: true
          },
          {
            title: 'day',
            key: 'day',
            sortable: true
          },
          {
            title: 'price',
            key: 'price',
            sortable: true
          },
          {
            title: 'receive_address',
            key: 'receive_address',
            sortable: true
          },
          {
            title: '操作',
            slot: 'action',
            align: 'center'
          }
        ],
        orderData: []
      }
    },
    mounted() {
      var url
      if(this.dataSource == 1){
        url = '/order/info'
      }
      else {
        url = '/g/order/info'
      }
      this.$axios
        .get(url)
        .then(response => {
          //Hive数据库原有的order
          this.orderData = response.data
          //前端新增的order
          for(var i = 0; i < this.newOrderList.length; i++){
            this.orderData.push(this.newOrderList[i])
          }
          //清掉前端的order缓存
          this.$emit('clearNewOrderList')
          this.$Message.success('成功加载Order数据')
        })
        .catch(error => {
          this.$Message.error('加载Order数据失败')
        })
    },
    methods: {
      upLoad(){
        var url
        if(this.dataSource == 1){
          url = '/order/update'
        }
        else {
          url = '/g/order/update'
        }
        this.$axios
            .post(url, {
              list: this.orderData
            })
            .then(response => {
              this.$Message.success("成功将Order数据同步至数据库")
            })
            .catch(error => {
              this.Message.error("同步Order数据失败！")
            })
      },
      // submit(){
      //   const newCoupon = {
      //     coupon_id: this.createCouponForm.coupon_id,
      //     discount: this.createCouponForm.discount,
      //     inventory: this.createCouponForm.inventory,
      //   }
      //   this.couponData.push(newCoupon)
      //   this.createCouponModal = false
      // },
      // createCoupon(){
      //   this.createCouponModal = true
      // },
      remove(index) {
        this.orderData.splice(index, 1);
      }
    }
  }
</script>

<style>
</style>



